/* Ï†ÑÏ≤¥ Í∏∞Î≥∏ Í∏ÄÍº¥ Î∞è Ìè∞Ìä∏ ÌÅ¨Í∏∞ ÏÑ§Ï†ï */
body {
    font-size: 18px;
    font-family: 'Noto Sans KR', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f9f9f9;
    padding: 80px 2rem;
}

.login-box {
    padding: 3rem;
    border-radius: 8px;
    width: 100%;
    max-width: 480px;
}

.login-title {
    font-size: 3rem; /* ÌôïÎåÄ */
    font-weight: bold;
    text-align: center;
    margin-bottom: 1rem;
}

.login-subtitle {
    text-align: center;
    color: #666;
    margin-bottom: 2rem;
    font-size: 1.3rem; /* ÌôïÎåÄ */
}

.login-form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.login-form label {
    font-size: 1.1rem; /* Ï∂îÍ∞Ä */
}

.login-form input[type="text"],
.login-form input[type="password"],
.login-form input[type="email"] {
    padding: 0.75rem;
    font-size: 1.1rem; /* ÌôïÎåÄ */
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
}

/* ‚úÖ Í∞ÑÍ≤© Ï¢ÅÌûàÍ∏∞ ÏúÑÌïú ÏàòÏ†ï */
small {
    font-size: 1rem; /* ÌôïÎåÄ */
    margin-top: 0.2rem; /* ÏÉÅÎã® Ïó¨Î∞± Ï§ÑÏù¥Í∏∞ */
    margin-bottom: 0.2rem; /* ÌïòÎã® Ïó¨Î∞± Ï§ÑÏù¥Í∏∞ */
    line-height: 1.4; /* line-height Ï§ÑÏó¨ÏÑú ÌÖçÏä§Ìä∏ Í∞ÑÍ≤© Ï¢ÅÌûàÍ∏∞ */
}

.password-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem; /* ÌôïÎåÄ */
}

.password-container a {
    color: #6a1b9a;
    text-decoration: none;
}

.remember-me {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1rem; /* ÌôïÎåÄ */
}

.login-form button {
    background-color: #6a1b9a;
    color: white;
    padding: 0.8rem;
    font-size: 1.1rem; /* ÌôïÎåÄ */
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.login-form button:hover {
    background-color: #8e24aa;
}

/* ‚úÖ Í≤πÏπ® Î¨∏Ï†ú Ìï¥Í≤∞Îêú Íµ¨Î∂ÑÏÑ† Ïä§ÌÉÄÏùº */
.divider {
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 1rem;
    color: #999;
    margin: 2rem 0 1.5rem;
    width: 100%;
}

.divider::before,
.divider::after {
    content: '';
    flex: 1;
    height: 1px;
    background: #ccc;
    margin: 0 1rem;
}

.social-buttons {
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.social-buttons button {
    flex: 1;
    padding: 0.6rem;
    font-size: 1.05rem; /* ÌôïÎåÄ */
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #eee;
    cursor: not-allowed;
}

.signup-link {
    text-align: center;
    font-size: 1.1rem; /* ÌôïÎåÄ */
    margin-top: 2rem;
}

.signup-link a {
    color: #6a1b9a;
    text-decoration: none;
}

/* üåô Î∞∞Í≤Ω Î∞è Ï†ÑÏ≤¥ ÌÖçÏä§Ìä∏ */
.dark-mode .login-container {
    background-color: #1e1e1e;
    color: #f0f0f0;
}

/* üåô Î°úÍ∑∏Ïù∏ Î∞ïÏä§ ÎÇ¥Î∂Ä Î∞∞Í≤Ω */
.dark-mode .login-box {
    background-color: #2a2a2a;
    color: #f0f0f0;
}

/* üåô ÏÑúÎ∏åÌÉÄÏù¥ÌãÄ */
.dark-mode .login-subtitle {
    color: #bbbbbb;
}

/* üåô ÏûÖÎ†• ÌïÑÎìú */
.dark-mode .login-form input[type="text"],
.dark-mode .login-form input[type="password"],
.dark-mode .login-form input[type="email"] {
    background-color: #1e1e1e;
    border: 1px solid #555;
    color: #f0f0f0;
}

/* üåô ÎßÅÌÅ¨ */
.dark-mode .password-container a,
.dark-mode .signup-link a {
    color: #b88eff;
}

/* üåô Î°úÍ∑∏Ïù∏ Î≤ÑÌäº */
.dark-mode .login-form button {
    background-color: #6a0dad;
    color: white;
}

.dark-mode .login-form button:hover {
    background-color: #5a0099;
}

/* üåô Íµ¨Î∂ÑÏÑ† */
.dark-mode .divider {
    color: #aaa;
}

.dark-mode .divider::before,
.dark-mode .divider::after {
    background: #444;
}

/* üåô ÏÜåÏÖú Î≤ÑÌäº */
.dark-mode .social-buttons button {
    background-color: #333;
    border: 1px solid #555;
    color: #aaa;
}
